				{name:'差评',number:1,type:'poor'},
				{name:'有图',number:12,type:'img'},
			],
			labelIndex:0,
			ratingsList:[
				{id:1,username:"B哥",face:"/static/img/face.jpg",date:'2019-11-21',spec:"自习室",grade:"good",
					first:{content:"很不错，之前去过很多次，环境很好",
